"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { ImageIcon, ZoomIn } from "lucide-react"
import { useState } from "react"

interface MedicalImagingDisplayProps {
  patientName: string
}

export function MedicalImagingDisplay({ patientName }: MedicalImagingDisplayProps) {
  const [selectedImage, setSelectedImage] = useState<string | null>(null)

  const imagingScans = [
    {
      id: "mri-brain",
      title: "MRI - Brain",
      type: "Brain Imaging",
      date: new Date(),
      imageUrl: "/mri-brain-scan.png",
      description: "High-resolution MRI scan of the brain",
    },
    {
      id: "ct-chest",
      title: "CT - Chest",
      type: "Chest Imaging",
      date: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000),
      imageUrl: "/ct-chest-scan-medical-imaging.jpg",
      description: "CT scan of the chest cavity",
    },
  ]

  return (
    <>
      <Card>
        <CardHeader>
          <div className="flex items-center gap-2">
            <ImageIcon className="h-5 w-5 text-primary" />
            <div>
              <CardTitle>Medical Imaging</CardTitle>
              <CardDescription>Recent diagnostic imaging scans for {patientName}</CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {imagingScans.map((scan) => (
              <div key={scan.id} className="space-y-3 border border-border rounded-lg p-4">
                <div className="flex items-center justify-between">
                  <h3 className="font-semibold text-sm">{scan.title}</h3>
                  <Badge variant="outline" className="text-xs">
                    {scan.type}
                  </Badge>
                </div>

                {/* Image Preview */}
                <div
                  className="relative w-full h-64 bg-slate-950 rounded-lg overflow-hidden border border-border/50 flex items-center justify-center group cursor-pointer hover:border-primary/50 transition-colors"
                  onClick={() => setSelectedImage(scan.imageUrl)}
                >
                  <img
                    src={scan.imageUrl || "/placeholder.svg"}
                    alt={scan.title}
                    className="object-cover w-full h-full"
                  />
                  <div className="absolute inset-0 bg-black/0 group-hover:bg-black/20 transition-colors flex items-center justify-center">
                    <div className="opacity-0 group-hover:opacity-100 transition-opacity">
                      <ZoomIn className="h-8 w-8 text-white/80" />
                    </div>
                  </div>
                </div>

                <p className="text-xs text-muted-foreground">{scan.description}</p>
                <p className="text-xs text-muted-foreground font-medium">
                  Acquired: {scan.date.toLocaleDateString()} at{" "}
                  {scan.date.toLocaleTimeString([], {
                    hour: "2-digit",
                    minute: "2-digit",
                  })}
                </p>
              </div>
            ))}
          </div>

          <div className="mt-6 p-4 bg-secondary/10 rounded-lg border border-secondary/20">
            <p className="text-xs text-muted-foreground">
              <span className="font-semibold">Clinical Note:</span> All medical imaging is displayed with HIPAA
              compliance. Images are encrypted and access is logged for audit trails.
            </p>
          </div>
        </CardContent>
      </Card>

      {/* Image Lightbox */}
      {selectedImage && (
        <div
          className="fixed inset-0 z-50 bg-black/80 flex items-center justify-center p-4"
          onClick={() => setSelectedImage(null)}
        >
          <div className="relative w-full h-full max-w-4xl max-h-[90vh]">
            <button
              onClick={() => setSelectedImage(null)}
              className="absolute top-4 right-4 text-white hover:text-gray-300 z-10"
            >
              <span className="text-2xl">Ã—</span>
            </button>
            <img
              src={selectedImage || "/placeholder.svg"}
              alt="Medical imaging fullscreen"
              className="w-full h-full object-contain"
            />
          </div>
        </div>
      )}
    </>
  )
}
