"use client"

import type { DiagnosticResult } from "@/lib/queries"
import { Card, CardContent } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Clock } from "lucide-react"

interface DiagnosticTimelineProps {
  results: DiagnosticResult[]
}

export function DiagnosticTimeline({ results }: DiagnosticTimelineProps) {
  const sortedResults = [...results].sort(
    (a, b) => new Date(b.timestampUtc).getTime() - new Date(a.timestampUtc).getTime(),
  )

  const getDiagnosisSeverity = (diagnosis: string) => {
    const lower = diagnosis.toLowerCase()
    if (lower.includes("critical") || lower.includes("severe")) return "destructive"
    if (lower.includes("warning") || lower.includes("alert")) return "default"
    return "secondary"
  }

  return (
    <div className="space-y-4">
      {sortedResults.map((result, index) => (
        <div key={result.id} className="flex gap-4">
          {/* Timeline indicator */}
          <div className="flex flex-col items-center">
            <div className="w-4 h-4 bg-primary rounded-full shadow-sm" />
            {index !== sortedResults.length - 1 && <div className="w-0.5 h-24 bg-border mt-2" />}
          </div>

          {/* Diagnostic content */}
          <Card className="flex-1 mb-4 hover:border-primary/50 transition-colors">
            <CardContent className="pt-5">
              <div className="flex justify-between items-start gap-4 mb-3">
                <Badge variant={getDiagnosisSeverity(result.diagnosis) as any} className="text-xs font-medium">
                  {result.diagnosis}
                </Badge>
                <div className="flex items-center gap-1 text-xs text-muted-foreground">
                  <Clock className="h-3 w-3" />
                  {new Date(result.timestampUtc).toLocaleDateString()} at{" "}
                  {new Date(result.timestampUtc).toLocaleTimeString([], {
                    hour: "2-digit",
                    minute: "2-digit",
                  })}
                </div>
              </div>
              {result.notes && <p className="text-sm text-foreground leading-relaxed">{result.notes}</p>}
              <div className="mt-4 pt-3 border-t border-border/50">
                <p className="text-xs text-muted-foreground font-mono">Record: {result.id.substring(0, 12)}...</p>
              </div>
            </CardContent>
          </Card>
        </div>
      ))}
    </div>
  )
}
