"use client"

import { useEffect, useState } from "react"
import { patientsApi } from "@/lib/api-client"
import { type PatientDetail } from "@/lib/queries"
import { PatientDetailsView } from "@/components/patient-details-view"
import { LoadingSpinner } from "@/components/loading-spinner"
import { Card, CardContent } from "@/components/ui/card"
import { AlertCircle } from "lucide-react"

interface PatientPageClientProps {
  patientId: string
}

export default function PatientPageClient({ patientId }: PatientPageClientProps) {
  const [patient, setPatient] = useState<PatientDetail | null>(null)
  const [error, setError] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const loadPatient = async () => {
      try {
        setIsLoading(true)
        const result = await patientsApi.getById(patientId)
        if (result.error) {
          setError(result.error)
        } else if (result.data) {
          setPatient(result.data)
        }
      } catch (err) {
        setError(err instanceof Error ? err.message : "Unknown error")
      } finally {
        setIsLoading(false)
      }
    }

    if (patientId) {
      loadPatient()
    }
  }, [patientId])

  if (error) {
    return (
      <Card className="border-destructive/50 bg-destructive/5 mb-6">
        <CardContent className="flex gap-3 pt-6">
          <AlertCircle className="h-5 w-5 text-destructive flex-shrink-0 mt-0.5" />
          <div>
            <p className="font-semibold text-destructive">Error loading patient</p>
            <p className="text-sm text-destructive/80">{error}</p>
          </div>
        </CardContent>
      </Card>
    )
  }

  if (isLoading) {
    return (
      <div className="flex justify-center py-12">
        <LoadingSpinner />
      </div>
    )
  }

  if (patient) {
    return <PatientDetailsView patient={patient} />
  }

  return (
    <Card>
      <CardContent className="pt-12 text-center">
        <p className="text-muted-foreground">Patient not found</p>
      </CardContent>
    </Card>
  )
}