"use client"

import type { PatientDetail } from "@/lib/queries"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { MedicalImagingDisplay } from "@/components/medical-imaging-display"
import { DiagnosticTimeline } from "@/components/diagnostic-timeline"
import { Calendar, Users, Stethoscope, AlertCircle } from "lucide-react"

export function PatientDetailsView({ patient }: { patient: PatientDetail }) {
  const patientAge = patient.age
  const createdDate = new Date(patient.createdAt)
  const totalDiagnoses = patient.diagnosticResults?.length || 0

  return (
    <div className="space-y-6">
      {/* Patient Header Information */}
      <Card className="border-primary/20 bg-gradient-to-r from-primary/10 via-primary/5 to-background">
        <CardHeader>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
            <div className="md:col-span-2">
              <CardTitle className="text-3xl mb-4">{patient.fullName}</CardTitle>
              <div className="space-y-3">
                <div className="flex items-center gap-3 text-foreground">
                  <Users className="h-4 w-4 text-primary" />
                  <span className="text-sm">
                    <span className="font-semibold">{patientAge}</span> years old
                  </span>
                </div>
                <div className="flex items-center gap-3 text-foreground">
                  <Calendar className="h-4 w-4 text-primary" />
                  <span className="text-sm">
                    Member since <span className="font-semibold">{createdDate.toLocaleDateString()}</span>
                  </span>
                </div>
              </div>
            </div>

            <div className="bg-card/50 rounded-lg p-4 border border-border/50">
              <p className="text-xs text-muted-foreground font-semibold uppercase tracking-wide mb-2">
                Medical Records
              </p>
              <div className="text-3xl font-bold text-primary">{totalDiagnoses}</div>
              <p className="text-xs text-muted-foreground">diagnostic records</p>
            </div>

            <div className="bg-card/50 rounded-lg p-4 border border-border/50">
              <p className="text-xs text-muted-foreground font-semibold uppercase tracking-wide mb-2">Current Status</p>
              {(patient.lastDiagnosis && (
                <Badge variant="default" className="text-xs">
                  {patient.lastDiagnosis}
                </Badge>
              )) || (
                <Badge variant="outline" className="text-xs">
                  No active diagnosis
                </Badge>
              )}
            </div>
          </div>
        </CardHeader>
      </Card>

      {/* Medical Imaging Section */}
      <MedicalImagingDisplay patientName={patient.fullName} />

      {/* Diagnostic History */}
      <Card>
        <CardHeader>
          <div className="flex items-center gap-3">
            <div className="p-2 bg-primary/10 rounded-lg">
              <Stethoscope className="h-5 w-5 text-primary" />
            </div>
            <div>
              <CardTitle>Diagnostic History</CardTitle>
              <CardDescription>
                Complete medical records and diagnoses {totalDiagnoses > 0 && `(${totalDiagnoses} records)`}
              </CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          {patient.diagnosticResults && patient.diagnosticResults.length > 0 ? (
            <DiagnosticTimeline results={patient.diagnosticResults} />
          ) : (
            <div className="text-center py-12">
              <AlertCircle className="h-12 w-12 text-muted-foreground/30 mx-auto mb-4" />
              <p className="text-muted-foreground">No diagnostic records found for this patient</p>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
